  z-index: 1001;
  background: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.preloader__row {
  width: 70px;
  height: 70px;
  text-align: center;
  margin-top: -35px;
  margin-left: -35px;
  animation: preloader-rotate 2s linear infinite;
  position: relative;
  top: 50%;
  left: 50%;
}

.preloader__item {
  background-color: var(--accent-color);
  width: 35px;
  height: 35px;
  border-radius: 100%;
  animation: preloader-bounce 2s ease-in-out infinite;
  display: inline-block;
  position: absolute;
  top: 0;
}

.preloader__item:last-child {
  animation-delay: -1s;
  top: auto;
  bottom: 0;
}

@keyframes preloader-rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes preloader-bounce {
  0%, 100% {
    transform: scale(0);
  }

  50% {
    transform: scale(1);
  }
}

.loaded_hiding .preloader {
  opacity: 0;
  transition: opacity .3s;
}

.loaded .preloader {
